              if (rUnderlineTypes.code == 0) {
                this.underlineTypes = rUnderlineTypes.data.typelist
              } else {
                // this.mAlert(rUnderlineTypes.message, () => { },"error");
              }
              if (rCompanyAccount.code == 0) {
                rCompanyAccount.data.forEach(ele => {
