        clipboard.on('error', function (e) {
          // _this.$message.error('éäºå¦ï¼è¿æ¯ä¸æ¡éè¯¯æ¶æ¯');
        })
      },
      checkFrom () {
        if (this.vmunderline.underlineAccountName == '') {
