          return
        }
        if (this.vmunderline.underlineBankType < 1) {
          this.mAlert('è¯·éæ©ææ¬¾æå¨é¶è¡')
          return
        }
        if (this.vmunderline.orderAmount.includes('.')) {
          //è¯·è¾å¥æ´æ°éé¢
          this.mAlert('è¯·è¾å¥æ´æ°éé¢')
